<template>
    <div>
        <slide-verify :l="42"
            :r="10"
            :w="310"
            :h="155"
            @success="onSuccess"
            @fail="onFail"
            @refresh="onRefresh"
            :slider-text="text"
            ></slide-verify>
        <div>{{msg}}</div>
    </div>
</template>
<script>

export default {
   name: 'slideverify',
    data(){
        return {
            msg: '',
            text: '',
        }
    },
    methods: {
        onSuccess(){
            this.msg = 'login success'
        },
        onFail(){
            this.msg = ''
        },
        onRefresh(){
            this.msg = ''
        }
    }
}
</script>

