<template>
    <div>
        <yuanhuan radius='' :item='item'/>
          <Table
            :data="tableData"
            :columns='columns'
            :height='200'
            :showHeader='true'
            style="width: 100%">
            <template
              prop="date"
              type='template' 
              label=""
              width="180">
            </template>
        </Table>
    </div>
</template>
<script>
import CircleMenu from 'vue-circle-menu'
import yuanhuan from './yuanhuan'
import Table from './table.vue'
  export default {
    watch:{
      radio(){
        console.log(this.radio)
      },
    },
    data(){
      return{
        radio:"1",
        item:{
          id:111,
          percent:50
        },
        tableData:[{
