                    },
                    {
                        title: 'Action',
                        key: 'action',
                        fixed: 'right',
                        width: 120,
                        render: (h, params) => {
                            return h('div', [
                                h('el-button', {
                                    props: {
                                        type: 'text',
                                        size: 'small'
                                    }
                                }, 'View'),
                                h('Button', {
                                    props: {
                                        type: 'text',
                                        size: 'small'
                                    }
                                }, 'Edit')
                            ]);
                        }
                    }],
      }
    },
    components:{
      CircleMenu,yuanhuan,Table
    },
    mounted(){
      
    },
    methods:{
        input(radio){
            this.radio = radio;
            console.log(this.radio)
        }
    }
  }
</script>

<style rel="stylesheet/scss" scoped lang="scss" scoped>
 
</style>


