<template>
  <div class="home">
    <el-col :span="12">
    <h5></h5>
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span></span>
        </template>
        <el-menu-item-group>
          <template slot="title"></template>
          <el-menu-item index="1-1">1</el-menu-item>
          <el-menu-item index="1-2">2</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="2">
          <el-menu-item index="1-3">3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <template slot="title">4</template>
          <el-menu-item index="1-4-1">1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="3" disabled>
        <i class="el-icon-document"></i>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title"></span>
      </el-menu-item>
    </el-menu>
  </el-col>
  </div>
</template>

<script>
  import Menu from './Menu.vue'

  export default {
    name: 'home',
    components: {
      Menu
    },
    data () {
      return {
        router:[
            {
                name: "About",
                path: "/about",
                children: [
                {
                    name: "About US",
                    path: "/about/us"
                },
                {
                    name: "About Comp",
                    path: "/about/company",
                    children: [
                    {
                        name: "About Comp A",
                        path: "/about/company/A",
                        children: [
                        {
                            name: "About Comp A 1",
                            path: "/about/company/A/1"
                        }
                        ]
                    }
                    ]
                }
                ]
            },
            {
                name: "Link",
                path: "/link"
            }
        ] 
      }
    }
  }
</script>
  
 


