<template>
    <div>
        <iframe   name="frame"   width="100%" scrolling="auto"
	style="margin-top: -4px;" onload="this.style.height=document.documentElement.clientHeight"
	height="100%" id="frame" frameBorder="no" :src="'./static/love/index.html'"></iframe>
    </div>
</template>
<script>
 
  
export default {
  name: 'love',
  data () {
    return {
       
    }
  },
  components: {
      
  },
  mounted() {
    
         document.getElementById('frame').style.height = document.documentElement.clientHeight+"px";
        function resize(){
           // document.getElementById('frame').style.height = document.documentElement.clientHeight+"px";
        }
		window.onresize = resize;
  },
  methods:{
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   #frame {
       border: 0px;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 91;
    }
</style> 
 
 
 
 


