<template>
    <div class="navMenu">
        <label v-for="item in router"  >
            <el-menu-item v-if="!item.childMenuList" :index="item.menuId">
                
                    <template slot="title"  >{{item.menuName}}</template>
               
                
            </el-menu-item>
            <el-submenu v-if="item.childMenuList" :index="item.menuId">
                <template slot="title">
                <span>{{item.menuName}}</span>
                </template>
                <Menu :router="item.childMenuList"></Menu>
            </el-submenu>
        </label>
    </div> 
</template>

<script>
  
 

  export default {
    name: "Menu",
    props: ["router"], // Menu  router 
    components: {   },
    data() {
      return {
        toggleShow: false // toggle 
      };
    },
    mounted(){
        console.log(this.router)
    },
    methods: {
      handleToggleShow() {
        //  toggle  handler
        this.toggleShow = !this.toggleShow;
      }
    }
  };
</script> 
<style lang="">
    .el-menu-item{
        padding: 0 20px;
    }
</style>


