<template>
    <div>
       <table  id="table-content">
            <thead>
                <tr>
                    <th></th>
                    <th class="red">11</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th> 
                    <th></th>
                </tr>
            </thead>
            <thead>
                <tr>
                    <th></th>
                    <th class="red">11</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th> 
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(info,index) in maintenance">
                    <td>{{index+1}}</td>
                    <td>{{info.maintenancetime}}</td>
                    <td>{{info.maintenancekm}}</td>
                    <td>{{info.maintenanceresult}}</td>
                    <td>{{info.maintenancemoney}}</td>
                    <td>{{info.handleUserName}}</td>
                    <td>
                        <span v-if="info.isconfirm=='0'"></span>
                        <span v-if="info.isconfirm=='1'"></span>
                    </td>
                    <td>{{info.confirmUserName}}</td>
                </tr>
            </tbody>
        </table>
   
        <el-button class="el-button-add" @click="exportReport()">
            Excel
        </el-button>
    </div>
</template>
<script>
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
   name: 'slideverify',
    data(){
        return {
            maintenance: [{"id":"20190801104454000000","maintenancetime":"2019-08-06","maintenancekm":"32","maintenanceresult":"3232","maintenancemoney":"323","handleuserid":"9","handleUserName":"","isconfirm":"0","maintenanceMsg":"3232"}],
        }
    },
    methods: {
        exportReport(){
            let et = XLSX.utils.table_to_book(document.getElementById('table-content')); //tableDOM
            let etout = XLSX.write(et, { 
                bookType: 'xlsx', 
                bookSST: true, 
                type: 'array' 
            });
            try {
                FileSaver.saveAs(new Blob([etout], { 
                    type: 'application/octet-stream' 
                }), 'trade-publish.xlsx');   //trade-publish.xlsx 
            } catch (e) {
                console.log(e, etout) ;
            }
            return etout;
        },
        
    }
}
</script>
<style  lang="scss">
    .red{
        color :red;
    }
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->
 
 
 


